<template>
  <div class="energyTrendAnalysis">
    <div class="right border-box">
      <div class="search chartSearch">
        <div>
          <el-date-picker
            v-model="date1"
            :type="pickerType"
            :value-format="valueFormat"
            placeholder=""
            @change="powerRankFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div>
          <el-date-picker
            v-model="date2"
            :type="pickerType"
            :value-format="valueFormat"
            placeholder=""
            @change="powerRankFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div class="tabAll">
          <div
            v-for="(item, index) in timeTab"
            :key="index"
            :class="{ isActive: index == tTIndex }"
            @click="changeTimeTab(index)"
          >
            {{ item }}
          </div>
        </div>
      </div>
      <div class="tabel">
        <el-table :data="tableData" style="width: 100%" height="250">
          <el-table-column prop="DateStr" :label="DateName"> </el-table-column>
          <el-table-column prop="PVStationId" label="电站编号">
          </el-table-column>
          <el-table-column prop="PVStationName" label="电站名称">
          </el-table-column>
          <el-table-column prop="ActiveHours" label="有效小时数">
          </el-table-column>
          <el-table-column prop="Efficiency" label="发电效率(统折容量)">
          </el-table-column>
        </el-table>
      </div>
      <div class="border-box chart" style="flex-grow: 1">
        <e-Chart :arry="arry"></e-Chart>
      </div>
    </div>
  </div>
</template>
<script>
import eChart from "./components/powerRankEchart.vue";
import {
  areaList,
  generateRank,
} from "@/network/powerGenerationEfficiencyRank";
export default {
  components: {
    eChart,
  },
  data() {
    return {
      date1: "",
      date2: "",
      areaOption: [],
      tableData: [],
      arry: {},
      timeTab: ["日", "月", "年"],
      tTIndex: 0,
      DateName: "小时",
      valueFormat: "yyyy-MM-dd",
      pickerType: "date",
    };
  },
  mounted() {
    this.date1 = this.date2 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.powerRankFun();
  },
  methods: {
    /**
     * 获取数据
     * **/
    powerRankFun() {
      let date1 = this.date1
      let date2 = this.date2
      // if (this.tTIndex == 1) {
      //   date1 = date1 + "-01";
      //   date2 = date2 + "-01";
      // } else if (this.tTIndex == 2) {
      //   date1 = date1 + "-01-01";
      //   date2 = date2 + "-01-01";
      // }
      generateRank(this.timeTab[this.tTIndex], date1, date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content.line;
            this.arry = content;
          }
        })
        .catch((err) => {});
    },
    changeTimeTab(index) {
      if (index == 0) {
        this.valueFormat = "yyyy-MM-dd";
        this.pickerType = "date";
        this.date1 = this.date2 =
          this.$getDate().year +
          "-" +
          this.$getDate().month +
          "-" +
          this.$getDate().day;
      } else if (index == 1) {
        this.valueFormat = "yyyy-MM";
        this.pickerType = "month";
        this.date1 = this.date2 =
          this.$getDate().year + "-" + this.$getDate().month;
      } else {
        this.valueFormat = "yyyy";
        this.pickerType = "year";
        this.date1 = this.date2 = JSON.stringify(this.$getDate().year);
      }
      this.tTIndex = index;
      this.powerRankFun();

      if (index == 0) {
        this.DateName = "小时";
      } else {
        this.DateName = "日期";
      }
    },
  },
};
</script>
