import { request } from "./request";
import Vue from 'vue'

export function areaList() {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/areaList",
        method: 'POST'
    })
}
export function powerRank(dateType, toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvStationTwoAction/powerRank?id=" + sessionStorage.getItem('companyId') + '&dateType=' + dateType + '&toDate=' + toDate + '&type=' + sessionStorage.getItem('companyType'),
        method: 'POST'
    })
}
export function generateRank(dateType, fromDate,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvStationTwoAction/generateRank?id=" + sessionStorage.getItem('companyId') + '&dateType=' + dateType + '&fromDate=' + fromDate+'&toDate=' + toDate + '&type=' + sessionStorage.getItem('companyType'),
        method: 'POST'
    })
}